import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import {
  trigger,
  state,
  style,
  animate,
  transition
} from '@angular/animations';

import { ProductService } from '../shared/product.service';
import { Information } from '../recommend/information.component';
import { HttpClient } from '@angular/common/http';
import { HttpParams } from '@angular/common/http';
import { Observable } from 'rxjs/Observable';
import { WebSocketService } from '../shared/web-socket.service';
import { JobSearchParams } from '../bean/JobSearchParams';
import { InforSkill } from '../bean/InforSkill';

@Component({
  selector: 'app-search',
  templateUrl: './search.component.html',
  styleUrls: ['./search.component.css'],
  animations: [
    trigger('btnState', [
      state('inactive', style({
        transform: 'scale(1)'
      })),
      state('active', style({
        backgroundColor: '#e06366',
        transform: 'scale(1.1)'
      })),
      transition('inactive <=> active', animate('300ms ease-in'))
    ])
  ]
})
export class SearchComponent implements OnInit {
  /* 发布时间 */
  catetories: string[];
  formModel: FormGroup;
  state = 'inactive';
  infors: Information[];
  btnValue = '搜索';
  private sillsInfor: InforSkill[];
  jsonStrings: Information;

  /* 查询参数 */
  private edus: string[];
  private releseTime: string[];
  private skills: string[];
  private workTime: string[];

  constructor(private fb: FormBuilder,
    private productService: ProductService,
    private http: HttpClient,
    private wsService: WebSocketService) {

    /* 表单模型结构 */
    this.formModel = fb.group({
      position: [null,
        [
          Validators.minLength(2),
          Validators.required
        ]
      ],
      comp: [null, Validators.minLength(2)],
      place: [null, Validators.minLength(2)],
      salary: [null, this.positiveNumberValidator],
      time: [null],
      exper: [null],
      edu: [null],
      skills: this.fb.array([])
    });

    /* 获得查询参数 */
    this.productService.getSearchParams().subscribe(
      data => {
        /* 将结果显示在界面 */
        this.edus = data.education;
        this.releseTime = data.releaseTime;
        this.skills = data.skills;
        this.workTime = data.workExperience;

        /* 遍历技能创建表单模型 */
        // console.log('测试skills 数据：'+ this.skills);
        // console.log('测试skills 数据：');
        // console.log(this.skills.map( str => {
        //   var obj: InforSkill = new InforSkill(str);
        //   return obj;          
        // }));
        this.sillsInfor = this.skills.map( str => {
          var obj: InforSkill = new InforSkill(str);
          return obj;          
        });
        // for (let i = 0; i < this.skills.length; i ++) {
        //   // this.sillsInfor.push(new InforSkill(this.skills[i]));
        //   // new InforSkill(this.skills[i]);
        //   // ;
        //   console.log(new InforSkill(this.skills[i]));
        //   // console.log(this.skills[i]);
        // } 
        console.log('Skills Obj: '); // "0", "1", "2",
        console.log(this.sillsInfor); // "0", "1", "2",
        /* 表单模型设置 */
        this.setSkills(this.sillsInfor);
      }
    )
    

    
  }



  /*验证输入数值是否为正数*/
  positiveNumberValidator(control: FormControl): any {
    if (!control.value) {
      return null;
    }
    const prices = parseInt(control.value, 10);
    if (prices > 0) {
      return null;
    } else {
      return { positiveNumber: true };
    }
  }
  /* 语言技能  */
  setSkills(skills: InforSkill[]) {
    const skillsFGs = skills.map(tempt => this.fb.group(tempt));
    const skillFormArray = this.fb.array(skillsFGs);
    this.formModel.setControl('skills', skillFormArray);
  }
  /* 改变搜索按钮文字 */
  toggleState() {
    this.state = this.state === 'active' ? 'inactive' : 'active';
    this.btnValue = this.btnValue === '搜索' ? '暂停' : '搜索';
  }
  /* 点击搜索按钮 */
  onSubmit() {
    if (this.formModel.valid) {
      console.log(this.formModel.value);
      this.productService.search(this.formModel.value).subscribe(date => {
        console.log("获得Job信息");
        console.log(date);
        this.infors = date;
      });
    } else {
      console.log('表单不合格 ： error');
    }
  }

  ngOnInit() {
  }

  /*恢复表单 - 文字*/
  revert() {
    this.formModel.reset({
      skills: this.sillsInfor
    });
  }
}




