import { Component, OnInit } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { RegisterOrLogin } from '../bean/RegisterOrLogin';


@Component({
  selector: 'app-login-ok',
  templateUrl: './login-ok.component.html',
  styleUrls: ['./login-ok.component.css']
})
export class LoginOKComponent implements OnInit {
  title: string = '...';
  constructor(private route: ActivatedRoute) { 
    let id: number;
    this.route.params.subscribe((params) => {
      id = params.id;
      console.log('id : ' + id + ' param : ' + params);
    });
    console.log('传入LoginOk的ID:'+ id);
    if(id == RegisterOrLogin.LOGIN) {
      this.title  = '已经登录';
    } else if (id == RegisterOrLogin.REGISTER) {
      this.title = '注册成功';
    }
  }

  ngOnInit() {
  }

}



