import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { ProductService } from '../shared/product.service';
import { MycookieService } from '../shared/mycookie.service';
import { RegisterOrLogin } from '../bean/RegisterOrLogin';

@Component({
  selector: 'app-enroll',
  templateUrl: './login.component.html',
  styleUrls: ['./login.component.css']
})
export class LoginComponent implements OnInit {
  formModel: FormGroup;
  constructor(
    private fb: FormBuilder, 
    private router: Router,
    private ps: ProductService,
    private cs: MycookieService
  ) {
    this.formModel = fb.group({
      username: [null, [Validators.required, this.usernameValidator]],
      password: [null, [Validators.required, Validators.minLength(6)]],
    });
  }

  ngOnInit() {

  }
  onSubmit() {
    if (this.formModel.valid) {
      console.log('登录表单数据： ');
      console.log(this.formModel.value);
      /* 提交表单数据 */
      /* 改变本页的样式 */
      /* 跳转至首页  */
      this.ps.getLoginInfor(this.formModel.value).subscribe(
        data => {
          if (data.isOK == 'Yes') {
            /* 登录成功，保存cookie */
            this.cs.setStrCookie('isOK', 'Yes');
            // this.cs.setStrCookie('personalCenterOK', 'No');
            /* 将当前页面显示为’登录‘成功 */
            this.router.navigate(['/loginOK', RegisterOrLogin.LOGIN]);
            console.log('是否登录' + this.cs.getCookie('isOK'));
          } else {
            console.log('登录失败，返回结果为：' + data.isOK);
          }
        }
      );
      // 
    } else {
      console.log(this.formModel.errors);
      console.log('登录表单存在错误');
    }
  }

  /*  校验邮编 和 校验电话*/
  usernameValidator(control: FormControl): any {
    if (!control.value)
      return null;
    var emailReq = /\w@\w*\.\w/;
    // var mobileReq = /^(13[0-9]{9})|(15[89][0-9]{8})$/;
    let emaiValid = emailReq.test(control.value);
    // let mobileValid = mobileReq.test(control.value);
    // if (emaiValid)
    console.log('邮箱校验' + emaiValid);
    return emaiValid ? null : { username: { errorInfor: '邮箱格式错误' } };
    // else (mobileValid)
    //   return mobileValid ? null : { mobile: { error: '电话号码错误' } };
  }
}



