import { Component, OnInit } from '@angular/core';
import { FormGroup, FormBuilder, FormControl, Validators } from '@angular/forms';
import { Router } from '@angular/router';
import { RegisterInfor } from '../bean/RegisterInfor';
import { ProductService } from '../shared/product.service';
import { MycookieService } from '../shared/mycookie.service';

@Component({
  selector: 'app-register',
  templateUrl: './register.component.html',
  styleUrls: ['./register.component.css']
})
export class RegisterComponent implements OnInit {
  formModel: FormGroup;
  constructor(
    private fb: FormBuilder, 
    private router: Router,
    private ps: ProductService,
    private cs: MycookieService
  ) {
    this.formModel = fb.group({
      username: [null, [Validators.required, this.usernameValidator]],
      passwords: fb.group({
        password: [null, [Validators.required, Validators.minLength(6)]],
        confirmPassword: [null, [Validators.required, Validators.minLength(6)]]
      }, { validator: this.equalValidator })
    });
  }

  ngOnInit() {

  } 
  onSubmit() {
    if (this.formModel.valid) {
      console.log('表单数据：');
      console.log(this.formModel.value);
      // console.log();
      // console.log(this.formModel.get('passwords').get('password').value)
      
      let submitData: RegisterInfor = new RegisterInfor(this.formModel.get('username').value, this.formModel.get('passwords').get('password').value);
      // submitData.password = ;
      // submitData.password = this.formModel.get('passwords').get('password').value;
      console.log(submitData);
      this.ps.getRegisterInfor(submitData).subscribe(
        data => {
          console.log('注册返回信息 isOK' + data.isOK);
          /* 注册成功， 将已经注册的flag 保存 */
          this.cs.setStrCookie('register', 'Yes');
          /* 跳转至提交个人信息页面 */
          this.router.navigate(['/person']);
        }
      )    
    } else {
      console.log('注册表单存在错误');
    }
  }

  /*  校验邮编 和 校验电话*/
  usernameValidator(control: FormControl): any {
    if (!control.value)
      return null;
    var emailReq = /\w@\w*\.\w/;
    // var mobileReq = /^(13[0-9]{9})|(15[89][0-9]{8})$/;
    let emaiValid = emailReq.test(control.value);
    // let mobileValid = mobileReq.test(control.value);
    // if (emaiValid)
    console.log('邮箱校验' + emaiValid);
    return emaiValid ? null : { username: { errorInfor: '邮箱格式错误' } };
    // else (mobileValid)
    //   return mobileValid ? null : { mobile: { error: '电话号码错误' } };
  }
  /*  校验前后两次输入密码正确  */
  equalValidator(group: FormGroup): any {
    const password = group.get('password').value;
    const pConfirm = group.get('confirmPassword').value;
    let validEqual: boolean = (password === pConfirm);
    console.log('两次输入密码比较' + validEqual);
    return validEqual ? null : { equal: { errorInfor: '前后两次输入密码不同' } };
  }
}



