import { Component, OnInit } from '@angular/core';
import { Router, ActivatedRoute } from '@angular/router';
import { MycookieService } from '../shared/mycookie.service';
import { ProductService } from '../shared/product.service';
import { PersonalInformation } from '../bean/PersonalInformation';
import { PersonalStatus } from '../bean/PersonalStatus';
import { RegisterOrLogin } from '../bean/RegisterOrLogin';

@Component({
  selector: 'app-navbar',
  templateUrl: './navbar.component.html',
  styleUrls: ['./navbar.component.css']
})
export class NavbarComponent implements OnInit {

  constructor(
    private router: Router,
    private cs: MycookieService,
    private route: ActivatedRoute,
    private ps: ProductService
  ) {


  }
  ngOnInit() {

  }
  /* 点击注册  */
  clickRegister(): boolean {
    if (this.cs.getCookie('isOK') == 'Yes' || this.cs.getCookie('register')== 'Yes') {
      this.router.navigate(['/loginOK', RegisterOrLogin.REGISTER]);
      console.log('导航进入 loginOK');
    } else {
      console.log('导航进入 register' + this.router.navigate(['/register']));
    }
    return false;
  }
  /* 点击登录 */
  clickLogin(): boolean {
    if (this.cs.getCookie('isOK') == 'Yes') {
      this.router.navigate(['/loginOK', RegisterOrLogin.LOGIN]);
      console.log('导航进入 loginOK');
    } else {
      console.log('导航进入 login' + this.router.navigate(['/login']));
    }
    return false;
  }
  /* 点击个人中心 */
  clickPersonCenter(): boolean {
    let statu: string;
    let personalResult: PersonalInformation;
    this.ps.getPersonalInformation().subscribe(
      data => {
        statu = data.status;
        personalResult = data.result;

        console.log('PersonalResult:');
        console.log(personalResult);
        console.log('PersonalStatus ： ' + statu);
        /* 已经登录，提交表单 */
        if (statu == PersonalStatus[2]) {
          console.log('导航进入 personOK' + this.router.navigate(['/personOK/personal-information']));
          this.cs.setStrCookie(PersonalStatus[2], statu);
          /* 已经登录，而且填完了表单，此时将数据发送到个人详情页面 */
          this.ps.resultEvent.emit(personalResult);

        } else if (statu == PersonalStatus[0]) {
          /* 已经登录， 未提交表单 */
          console.log('导航进入 person' + this.router.navigate(['/person']));
          this.cs.setStrCookie(PersonalStatus[0], statu);

        } else {
          /* 没有登录 */
          console.log('导航进入 loginNo' + this.router.navigate(['/loginNo']));
          this.cs.setStrCookie(PersonalStatus[1], statu);

        }
      }
    ) 

    return false;
  }
}




